const store = require('store2')
const conf = require('./defaults')
// TODO obfuscate this for reverese security
const KEY = '69$hsYgrj_K1xhm-yHC45@GcgN=mPMX+NANNmu=4llIKL@WPZ4'
const platform = require('platform')

const util = {

  incLaunchCount() {
    const storage = util.encryptedStorage().get(conf.STORAGE_KEY)

    storage.launchCount = storage.launchCount || 0
    storage.launchCount++
    util.encryptedStorage().set(conf.STORAGE_KEY, storage)
  },

  getStorageKey(key) {
    return util.encryptedStorage().get(conf.STORAGE_KEY)[key]
  },

  removeStorageKey(key) {
    const storage = util.encryptedStorage().get(conf.STORAGE_KEY)

    delete storage[key]
    util.encryptedStorage().set(conf.STORAGE_KEY, storage)
  },

  updateStorage(key, value) {
    const storage = util.encryptedStorage().get(conf.STORAGE_KEY)

    storage[key] = value
    util.encryptedStorage().set(conf.STORAGE_KEY, storage)
  },

  resetStorage() {
    const {launchCount, launchTime} = util.encryptedStorage().get(conf.STORAGE_KEY)

    util.encryptedStorage().set(conf.STORAGE_KEY, {
      launchCount,
      launchTime
    })
  },

  isValidUserId(uid) {
    if (!uid) {
      return false
    }
    if (uid.length <= 3 || uid.length > 64) {
      return false
    }
    return !(/\s|\/|\\|\+|\*|#/ig).test(uid)
  },

  encryptedStorage() {
    return {
      get: key => {
        const val = store.get(key)

        if (!val) return {}
        return JSON.parse(this.decrypt(store.get(key)))
      },
      set: (key, val) => {
        val = typeof val === 'object' ? JSON.stringify(val) : val
        store.set(key, this.encrypt(val))
        return this
      }
    }
  },
  decrypt(text) {
    return String(text)
      .split('')
      .map((char, index) => String.fromCharCode(char.charCodeAt(0) - KEY.charCodeAt(index % KEY.length)))
      .join('')
  },
  encrypt(text) {
    return String(text)
      .split('')
      .map((char, index) => String.fromCharCode(char.charCodeAt(0) + KEY.charCodeAt(index % KEY.length)))
      .join('')
  },

  getAppDomain(appId, devMode) {
    if (devMode) {
      return conf.HOST_DEV
    }

    if (conf.EXCLUSIVE_APPIDS.indexOf(appId) === -1) {
      return appId.replace('-demo', '') + '.' + conf.HOST
    }
    return conf.HOST
  },

  defaultDevice({appId, userId, token, realtime, error}) {
    token = token || 'disabled'
    let tokenStatus = 'ALLOWED'

    const connection = navigator.connection || navigator.mozConnection || navigator.webkitConnection || {type: null}

    if (typeof token === 'object') { // Valid token format
      token = JSON.stringify(token)
    } else if (token === 'DENIED') { // push permission denied
      tokenStatus = token
      token = 'NOT_EXIST'
    } else if (token.startsWith('NOT_SUPPORTED')) {
      tokenStatus = token
      token = 'NOT_EXIST'
    }

    if (error) {
      tokenStatus = 'ERR'
      token = 'NOT_EXIST'
    }

    const installation = {
      realtime: realtime,
      launchCount: +this.encryptedStorage().get(conf.STORAGE_KEY).launchCount,
      launchTime: Date.now(),
      appId: appId,
      userId: userId,
      deviceToken: token,
      deviceType: 'web',
      tokenStatus: tokenStatus,
      tokenErr: error ? { message: error.message, code: error.code} : null,
      deviceModel: platform.name ? platform.name.toLowerCase() : 'NodeJS',
      osVersion: platform.os.version,
      deviceManufacture: platform.os.family,
      browserVersion: platform.version,
      appVersion: require('../package.json').version,
      subscriptions: conf.DEFAULT_CHANNELS,
      timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      connection: connection.type
    }

    if (token === '$omit_token') { // for usage in setUserInfo
      delete installation.deviceToken
      delete installation.tokenStatus
      delete installation.tokenErr
    }

    return installation
  },

  toTopicName(appId, userId, channel = '', type = 'user') {
    let section, tail

    switch (type) {
      case 'user':
        const parts = channel.split('/')

        if (parts[0] === 'public') { // app/APP_ID/user/public/CHANNEL
          section = parts[0]
          tail = parts[1]
        } else { // app/APP_ID/user/USER_ID/CHANNEL
          section = userId
          tail = parts[0]
        }
        break
      case 'publish': // app/APP_ID/publish/[USER_ID||public]/CHANNEL
        section = userId === '*' ? 'public' : userId
        tail = channel
        break
      case 'event': // app/APP_ID/event/channel/[+||userId]
        section = channel
        tail = userId ? userId : '+'
        break
      case 'track': // app/APP_ID/track/channel/[+||userId]
        section = channel
        tail = userId ? userId : '+'
        break
      default:
        throw new Error('Invalid topic type.')
    }

    return ['app', appId, type, section, tail].join('/')
  },

  fromChabokChannel(channel) {
    const parts = channel.split('/')
    let topic

    switch (parts.length) {
      case 1: // public
        // wall => public/wall
        topic = 'public/' + parts[0]
        break
      case 2: // public || private
        if (parts[0] !== 'public' && parts[0] !== 'private') {
          throw new Error('Invalid channel name format, please read documentations.')
        }
        //         public/wall => public/wall || private/wall => wall
        topic = parts[0] === 'public' ? 'public/' + parts[1] : parts[1]
        break
      default: // invalid format
        throw new Error('Invalid channel name format, please read documentations.')
    }
    return topic
  },

  toChabokMessage(topic, payload) {
    const topics = topic.split('/')
    const channel = topics[3] + '/' + topics[4]

    try {
      const msg = JSON.parse(payload)

      msg.channel = channel
      return msg
    } catch (e) {
      console.error('Invalid Chabok Payload ', payload)
      return payload
    }
  },

  toEventMessage(topic, payload) {
    const topics = topic.split('/')
    const appId = topics[1]
    const eventName = topics[3]
    const deviceId = topics[4]

    const event = {
      appId,
      eventName,
      deviceId,
      receivedAt: Date.now()
    }

    try {
      event.data = JSON.parse(payload)
      event.createdAt = event.data.createdAt
      event.id = event.data.id
      return event
    } catch (e) {
      console.error('Invalid Chabok event ', payload)
      return payload
    }
  }

}

module.exports = util



