const EventEmitter = require('events')
const TOKEN_SUBSCRIPTION = 'tokenSubscription'
const TOKEN_NOT_EXIST = 'NOT_EXIST'

let _instance = null

export default class PushNotification extends EventEmitter {

  constructor() {
    super()

    this.registerServiceWorker()
    if (_instance) {
      return _instance
    }
    _instance = this
  }

  initializeState() {
    if (!('showNotification' in ServiceWorkerRegistration.prototype)) {
      console.error('Notifications arent supported.')

      // check browser support here
      // https://developer.mozilla.org/en-US/docs/Web/API/ServiceWorkerRegistration/showNotification
      this.emit(TOKEN_SUBSCRIPTION, null, 'NOT_SUPPORTED:SN')
      return
    }

    // If the current notification permission is denied,
    // it's a permanent block until the user changes the permission
    if (Notification.permission === 'denied') {
      console.error('The user has blocked notifications.')
      // TODO may be token is available?
      this.emit(TOKEN_SUBSCRIPTION, null, 'DENIED')
      return
    }
    // Check if push messaging is supported
    if (!('PushManager' in window)) {
      console.error('Push messaging isnt supported.')
      this.emit(TOKEN_SUBSCRIPTION, null, 'NOT_SUPPORTED:PM')
      return
    }
    navigator.serviceWorker.ready.then((serviceWorkerRegistration) => {
      serviceWorkerRegistration.pushManager.getSubscription()
        .then((subscription) => {
          // Do we already have a push message subscription?
          this.sendSubscriptionToServer(subscription)
        })
        .catch((error) => {
          console.error('Error during getSubscription()', error)

          error.code = 203
          this.emit(TOKEN_SUBSCRIPTION, error, TOKEN_NOT_EXIST)
        })
    })
  }

  buildApplicationServerKey(VAPid) {
    const base64 = VAPid
    const rfc4648 = base64.replace(/-/g, '+').replace(/_/g, '/')
    const characters = atob(rfc4648).split('').map(character => character.charCodeAt(0))

    return new Uint8Array(characters)
  }

  sendSubscriptionToServer(subscription) {
    if (!subscription) {
      this.emit(TOKEN_SUBSCRIPTION, null, TOKEN_NOT_EXIST)
      return
    }

    try {
      this.emit(TOKEN_SUBSCRIPTION, null, subscription.toJSON())
    } catch (err) { // this error probably wont happened
      err.code = 204
      this.emit(TOKEN_SUBSCRIPTION, err, TOKEN_NOT_EXIST) // invalid token
    }
  }

  removeSubscriptionFromServer(subscription) {
    // todo: This is where you'd remove the subscription from the server.
  }

  registerServiceWorker() {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.register('/ChabokSDKWorker.js')
        .then((registration) => {
          this.initializeState()
          console.log('ServiceWorker registration successful.', registration)
          return navigator.serviceWorker.ready
        })
        .catch(error => {
          console.error('ServiceWorker registration failed.', error)

          error.code = 200
          this.emit(TOKEN_SUBSCRIPTION, error, TOKEN_NOT_EXIST)
        })
    } else {
      this.emit(TOKEN_SUBSCRIPTION, null, 'NOT_SUPPORTED:SW')
    }
  }

  permissionGranted(publicKey) {
    if ('serviceWorker' in navigator && navigator.serviceWorker.ready) {
      navigator.serviceWorker.ready.then((serviceWorkerRegistration) => {
        if (!serviceWorkerRegistration ||
          !serviceWorkerRegistration.pushManager ||
          !serviceWorkerRegistration.pushManager.subscribe) {

          this.emit(TOKEN_SUBSCRIPTION, null, 'NOT_SUPPORTED:PM')
          return
        }

        serviceWorkerRegistration
          .pushManager
          .subscribe({
            userVisibleOnly: true,
            applicationServerKey: this.buildApplicationServerKey(publicKey)
          })
          .then(subscription => {
            this.sendSubscriptionToServer(subscription)
          })
          .catch(error => {
            console.error('Unable to subscribe to messaging server.', error)

            error.code = 201
            this.emit(TOKEN_SUBSCRIPTION, error, TOKEN_NOT_EXIST)
          })
      })
    } else {
      this.emit(TOKEN_SUBSCRIPTION, null, 'NOT_SUPPORTED:SW')
    }
  }

  permissionDenied() {
    this.invalidateToken()
  }

  requestToken({publicKey}) {
    if ((typeof Notification === 'undefined' || !Notification) ||
      (!Notification.permission && !Notification.requestPermission)) {

      this.emit(TOKEN_SUBSCRIPTION, null, 'NOT_SUPPORTED:NOTIF')
      return
    }

    if (Notification.permission === 'denied') {
      this.permissionDenied()
      return
    }

    if (Notification.permission === 'default') {
      Notification
        .requestPermission()
        .then((result) => {
          if (result !== 'granted') {
            this.permissionDenied()
            return
          }
          this.permissionGranted(publicKey)
        })
      return
    }
    this.permissionGranted(publicKey)
  }

  invalidateToken() {
    // TODO when we are invalidating? and check error codes
    navigator
      .serviceWorker
      .ready
      .then((serviceWorkerRegistration) => {
        serviceWorkerRegistration.pushManager.getSubscription()
          .then((subscription) => {
            if (!subscription) {
              this.emit(TOKEN_SUBSCRIPTION, null, TOKEN_NOT_EXIST)
              return
            }
            subscription.unsubscribe().then(() => {
              this.emit(TOKEN_SUBSCRIPTION, null, TOKEN_NOT_EXIST)
            }).catch((error) => {
              console.error('Unable to unsubscribe to messaging server.', error)

              error.code = 202
              this.emit(TOKEN_SUBSCRIPTION, error, TOKEN_NOT_EXIST)
            })
            this.removeSubscriptionFromServer()
          })
          .catch((error) => {
            console.error('Error during getSubscription()', error)

            error.code = 203
            this.emit(TOKEN_SUBSCRIPTION, error, TOKEN_NOT_EXIST)
          })
      })
  }

}



