'use strict'
const Symbol = require('es6-symbol')
const axios = require('axios')
const mqtt = require('mqtt')
const EventEmitter = require('events')
const util = require('./util')
const shortid = require('shortid')
const conf = require('./defaults')
const Push = require('./pushNotification')

const init = Symbol()
const connectBroker = Symbol()
const publish = Symbol()
const publishMessage = Symbol()
const subscribe = Symbol()
const unSubscribe = Symbol()
const onConnect = Symbol()
const onMessage = Symbol()
const tagsApi = Symbol()
const reset = Symbol()
const updateInstallation = Symbol()
const createInstallation = Symbol()
const getInstallation = Symbol()
const handleToken = Symbol()
const disconnect = Symbol()
const handleBrowserState = Symbol()
const afterInstallationSaved = Symbol()

const CACHE_SIZE = 1000
let _instance = null

class Chabok extends EventEmitter {

  constructor(auth, config) {
    super()

    if (_instance) {
      return _instance
    }

    const DEFAULT_CONFIG = {
      realtime: true,
      webpush: {
        enabled: true,
        publicKey: null
      },
      silent: false
    }

    this.config = Object.assign(DEFAULT_CONFIG, config)
    this.push = new Push()
    this.auth = auth

    if (auth.hasOwnProperty('apiKey') &&
        auth.hasOwnProperty('username') &&
        auth.hasOwnProperty('password') &&
        !auth.hasOwnProperty('webKey')) {
      const err = new Error('Chabok error: this method of authentication is deprecated since v1.1.0,' +
        ' please check http://bit.ly/2V6RUYl')

      err.code = 100
      throw err
    }

    if (!auth.appId || typeof auth.appId !== 'string') {
      const err = new Error('Chabok error: invalid appId')

      err.code = 101
      throw err
    }

    const { publicKey } = this.config.webpush

    if (this.config.webpush.enabled &&
      !(typeof publicKey === 'string' && publicKey.length > 0)) {
      const err = new Error('Chabok error: cannot initialize Chabok. publicKey required when web push is enabled')

      err.code = 102
      throw err
    }

    this._createdAt = Date.now()
    this.isForground = true
    this[init]()
    this[handleBrowserState]()
    _instance = this
  }

  register(userId) {
    return new Promise((resolve, reject) => {
      if (!util.isValidUserId(userId)) {
        const err = new Error('Chabok error: invalid userId')

        err.code = 103
        return reject(err)
      }

      if (this.isRegistered() && this.getUserId() !== userId) {
        const currentUserId = this.getUserId()

        this[reset]()
        util.updateStorage('reregisteredFrom', currentUserId)
      }

      this.userId = userId
      const {appId} = this.auth
      const {webpush} = this.config

      if (!appId) { // this err is probably wont happened after
        const err = new Error('Chabok error: invalid appId')

        err.code = 101
        return reject(err)
      }

      return new Promise((res, rej) => {
        if (!webpush.enabled) {
          return res(this[getInstallation]())
        }

        this.push.once('tokenSubscription', (err, token) => {
          return res(this[handleToken](appId, userId, err, token))
        })
        return this.push.requestToken({publicKey: this.config.webpush.publicKey})
      })
        .then((installation) => {
          if (installation.id) {
            resolve(this[updateInstallation](installation))
          } else {
            resolve(this[createInstallation](installation))
          }
        })
    })
  }

  addTag(tag) {
    return this[tagsApi]('addTags', tag)
  }

  addTags(...tags) {
    return this[tagsApi]('addTags', ...tags)
  }

  removeTag(tag) {
    return this[tagsApi]('removeTags', tag)
  }

  removeTags(...tags) {
    return this[tagsApi]('removeTags', ...tags)
  }

  isRegistered() {
    return this.getInstallationId() != null
  }

  getInstallationId() {
    const {deviceId} = util.encryptedStorage().get(conf.STORAGE_KEY)

    return deviceId
  }

  getInstallations(ids) {
    if (!ids || ids.length === 0) {
      return []
    }

    return this.webClient.get('installations', {
      params: {
        filter: {
          where: {
            id: {
              inq: ids
            }
          }
        }
      }
    })
      .then(resp => resp.data)
  }

  publish(message) {
    return this[publishMessage](message)
  }

  publishEvent(type, data) {
    this[publish]('event', type, data)
  }

  track(type, data) {
    this[publish]('track', type, data)
  }

  setUserInfo(info) {
    if (!info || typeof info !== 'object') {
      const err = new Error('Chabok error: invalid userInfo')

      err.code = 104
      return Promise.reject(err)
    }

    const installation = this[getInstallation]('$omit_token', null)

    if (!installation.id) {
      const err = new Error('Chabok error: user is not registered')

      err.code = 105
      return Promise.reject(err)
    }

    installation.userInfo = info
    return this[updateInstallation](installation)
      .then(response => {
        if (response) {
          return true
        }
        return false
      })
  }

  getUserInfo() {
    const id = this.getInstallationId()

    if (!id) {
      const err = new Error('Chabok error: user is not registered')

      err.code = 105
      return Promise.reject(err)
    }

    return this.webClient.get(`installations/${id}`)
      .then(response => {
        if (response && response.data) {
          return response.data.userInfo
        }
        return undefined
      })
  }

  getTags() {
    const userId = this.getUserId()

    if (!userId) {
      const err = new Error('Chabok error: user is not registered')

      err.code = 105
      return Promise.reject(err)
    }

    return this.webClient.get(`installations/tags/${userId}`)
      .then(response => {
        return response.data || []
      })
  }
  /*
    channel:
      alert           public
      public/alert    public
      private/alert   private
   */
  subscribe(channel, live) {
    live = live || false
    const name = util.fromChabokChannel(channel)

    this[subscribe]({name, type: 'user', live})
  }

  unSubscribe(channel) {
    const name = util.fromChabokChannel(channel)

    this[unSubscribe]({name, type: 'user'})
  }

  subscribeEvent(eventName, installationId, live) {
    if (live === undefined) {
      live = true
    }

    this[subscribe]({
      name: eventName,
      userId: installationId,
      live: live,
      type: 'event'
    })
  }

  unSubscribeEvent(eventName, installationId) {
    this[unSubscribe]({
      name: eventName,
      userId: installationId,
      type: 'event'
    })
  }

  /**
   * unRegister chabok
   */
  unregister() {
    this.webClient.delete(`installations/${this.getInstallationId()}`)
    this[reset]()
    // TODO invalidateToken
  }

  [reset]() {
    if (this.client) {
      this.client.end()
    }

    util.resetStorage()
  }

  isAlreadyReceived(message) {
    if (!message.id) {
      return false
    }
    return checkAndAdd(message.id)

    function checkAndAdd(id) {
      const settings = util.encryptedStorage().get(conf.STORAGE_KEY)
      const cache = settings.receivedIds || new Array(CACHE_SIZE)
      const exists = cache.indexOf(id) > -1

      cache[cache.length % CACHE_SIZE] = id
      settings.receivedIds = cache
      util.encryptedStorage().set(conf.STORAGE_KEY, settings)
      return exists
    }
  }

  errorHandler(err, something) {
    if (err) {
      console.error('Error ', err)
      this.emit && this.emit('error', err)
    }
  }

  dismiss() {
    this[disconnect]()
    _instance = null
  }

  /**
   *
   * @param data
   * @param timeout
   */
  showLocalNotification(data = {}, timeout) {
    const title = data.title || ' '
    const options = {
      body: data.body,
      icon: data.icon || 'http://chabokpush.com/www/image/chabok-logo-big.png'
    }

    if (data.groupId) {
      options.tag = data.groupId
    }

    const NOTIFY_DISMISS_TIMEOUT_IN_MS = timeout || 5000

    if (typeof Notification === 'undefined' || !Notification || !Notification.requestPermission) {
      console.error('Notification does not supported')
      return
    }

    Notification
      .requestPermission(permission => {
        if (permission === 'granted') {
          const p = new Notification(title, options)

          if (data.clickUrl) {
            p.onclick = _ => {
              window.open(data.clickUrl, '_blank')
            }
          }
          setTimeout(p.close.bind(p), NOTIFY_DISMISS_TIMEOUT_IN_MS)
        }
      })
  }

  /**
   *  Get User ID
   * @return {*}
   */
  getUserId() {
    const {userId} = util.encryptedStorage().get(conf.STORAGE_KEY)

    return userId
  }

  [init]() {
    const {appId, webKey, devMode} = this.auth
    const API = conf.BASE_URL.replace('${host}', util.getAppDomain(appId, devMode))

    util.incLaunchCount()

    this.webClient = axios.create({
      baseURL: API,
      timeout: 15000,
      headers: {'X-Access-Token': webKey}
    })

    // const registeredUserId = this.getUserId()
    //
    // if (registeredUserId) {
    //   this.register(registeredUserId)
    // }
  }

  [disconnect]() {
    if (this.client) {
      this.client.once('connect', () => this.client.end())
      this.client.end()
    }
  }

  /**
   * run after installation resolved
   * @param installation
   * @return {{deviceId: *}}
   */
  [afterInstallationSaved](installation) {
    const {realtime} = this.config
    const settings = util.encryptedStorage().get(conf.STORAGE_KEY)

    this.deviceId = installation.id
    settings.deviceId = this.deviceId
    settings.userId = installation.userId
    util.encryptedStorage().set(conf.STORAGE_KEY, settings)
    this.emit('registered', this.deviceId)
    if (realtime) {
      this[connectBroker]()
    }
    return {deviceId: this.deviceId}
  }

  [updateInstallation](installation) {
    const {id} = installation

    if (!id) {
      return this[createInstallation](installation)
    }

    return this.webClient.put(`installations/${id}`, installation)
      .then(response => {
        installation.id = response.data.id
        return this[afterInstallationSaved](installation)
      })
      .catch(error => {
        const code = error.response.status

        if (code === 404) {
          return this[createInstallation](installation)
        }

        console.error('Error registering ', error)
        throw error
      })
  }

  [createInstallation](installation) {
    const reregisteredFrom = util.getStorageKey('reregisteredFrom')

    if (reregisteredFrom) {
      installation.reregisteredFrom = reregisteredFrom
      util.removeStorageKey('reregisteredFrom')
    }

    return this.webClient.post('installations', installation)
      .then(response => {
        installation.id = response.data.id
        return this[afterInstallationSaved](installation)
      })
      .catch(error => {
        console.error('Error registering ', error)
        throw error
      })
  }

  [getInstallation](token, error) {
    const {appId} = this.auth
    const {realtime} = this.config
    const userId = this.userId
    const {deviceId} = util.encryptedStorage().get(conf.STORAGE_KEY)
    const installation = util.defaultDevice({appId, userId, token, realtime, error})

    if (deviceId) {
      installation.id = deviceId
    }

    return installation
  }

  [handleToken](appId, userId, err, token) {
    if (!token) {
      return this[getInstallation](token, err)
    }

    if (typeof token === 'object') {
      token = Object.assign(token, {expirationTime: 0})
      this.emit('subscriptionToken', token) // for users usage
    }

    return this[getInstallation](token, err)
  }

  /**
   * Connect to MQtt Broker
   */
  [connectBroker]() {
    if (!this.deviceId || !this.userId) {
      console.warn('No deviceId/userId to connect!')
      return
    }

    if (this.client && this.client.connected) {
      return
    }

    const {webKey, appId, devMode} = this.auth
    const options = {
      clientId: [appId, this.userId, this.deviceId].join('/'),
      clean: false,
      keepalive: 180,
      reconnectPeriod: 3000,
      connectTimeout: 15 * 1000,
      username: webKey,
      password: webKey,
      wsOptions: {}
    }
    const B_HOST = conf.BROKER_ADDRESS.replace('${host}', util.getAppDomain(appId, devMode))

    this.client = mqtt.connect(B_HOST, options)

    this.client.on('connect', this[onConnect].bind(this))

    this.client.on('message', this[onMessage].bind(this))

    this.client.on('reconnect', _ => {
      console.log('Chabok Client Reconnecting')
      this.emit('connecting')
    })

    this.client.on('close', _ => {
      this.emit('closed')
    })

    this.client.on('error', err => {
      console.error('Error' + err.message)
      this.emit('error', err)
    })

    this.client.on('offline', function () {
      console.log('Chabok Client Offline')
      this.emit('disconnected')
    })
  }

  /**
   *
   */
  [onConnect]() {
    const {appId} = this.auth
    const deviceTopic = util.toTopicName(appId, this.userId, this.deviceId, 'user')
    const topics = conf.DEFAULT_CHANNELS.map(ch => util.toTopicName(appId, this.userId, ch))
    const subs = {}

    topics.push(deviceTopic)
    topics.forEach(topic => {
      subs[topic] = 1
    })

    if (!this.isForground) {
      return this[disconnect]()
    }
    // TODO do not subscribe on each connect for clean=false clients
    return this.client.subscribe(subs, _ => {
      this.emit('connected')
    })
  }

  /**
   *
   * @param topic
   * @param payload
   * @return {*}
   */
  [onMessage](topic, payload) {
    const parts = topic.split('/')

    if (parts.length === 5) {
      if (parts[2] === 'user') {
        const chabokMessage = util.toChabokMessage(topic, payload)

        // TODO dedup messages
        if (!this.config.silent && !chabokMessage.silent) {
          const notification = {
            body: chabokMessage.content,
            groupId: chabokMessage.channel
          }

          if (chabokMessage.notification) {
            notification.title = chabokMessage.notification.title
            notification.badge = chabokMessage.notification.badge

            if (chabokMessage.notification.text) { // alertText
              notification.body = chabokMessage.notification.text
            }
            if (chabokMessage.notification.body) {
              notification.body = chabokMessage.notification.body
            }
            if (chabokMessage.notification.icon) {
              notification.icon = chabokMessage.notification.icon
            }
            if (chabokMessage.notification.clickUrl) {
              notification.clickUrl = chabokMessage.notification.clickUrl
            }
            if (chabokMessage.notification.groupId) {
              notification.groupId = chabokMessage.notification.groupId
            }
          }

          this.showLocalNotification(notification)
        }
        return this.emit('message', chabokMessage)
      }

      if (parts[2] === 'event') {
        const event = util.toEventMessage(topic, payload)

        if (!this.isAlreadyReceived(event)) {
          return this.emit(parts[3], event)
        }
      }
    }

    return console.warn('Swallow message on topic', topic)
  }

  /**
   *
   * @param message properties are channel, user, live, content, data, stateful, ...
   */
  [publishMessage](message) {
    return new Promise((resolve, reject) => {
      if (!message || !message.user) {
        const err = new Error('Chabok error: invalid Message, please provide user')

        err.code = 106
        reject(err)
      }
      if (!message.channel) {
        const err = new Error('Chabok error: invalid channel name')

        err.code = 107
        reject(err)
      }
      if (!message.id) {
        message.id = Date.now()
      }
      if (!message.createdAt) {
        message.createdAt = Date.now()
      }

      const topic = util.toTopicName(this.auth.appId, message.user, message.channel, 'publish')
      const payload = JSON.stringify(message)

      if (!this.client) {
        return this[publishMessage].bind(this)(message)
      }

      return this.client.publish(topic, payload, {
        qos: message.live ? 0 : 1,
        retain: message.stateful
      }, (err, res) => {
        if (err) {
          return reject(err)
        }
        return resolve(res.payload)
      })
    })
  }

  /**
   *
   * @param type
   * @param data
   * @return {MqttClient | *}
   */
  [publish](topicType, type, data) {
    if (!this.client) {
      return this.once('connected', () => this[publish].bind(this)(topicType, type, data))
    }
    data = data || {}
    const topic = util.toTopicName(this.auth.appId, this.deviceId, type, topicType)
    const live = (data.live !== undefined) ? data.live : true
    const stateful = data.stateful

    data.id = data.id || shortid.generate()
    delete data.live
    delete data.stateful
    const payload = JSON.stringify(data)

    return this.client.publish(topic, payload, {
      qos: live ? 0 : 1,
      retain: stateful
    }, this.errorHandler.bind(this))
  }

  [handleBrowserState]() {
    const {realtime} = this.config

    // TODO improve logic
    if (realtime && window) {
      window.addEventListener('focus', () => {
        this.isForground = true
        if (this.timer) {
          clearTimeout(this.timer)
        }
        if (this.userId) {
          this[connectBroker]()
        }
      })
      window.addEventListener('blur', () => {
        this.isForground = false
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          this[disconnect]()
        }, 1000)
      })
    }
  }

  /**
   *
   * @param subObjects
   * @return {MqttClient | *}
   */
  [subscribe](subObjects) {
    if (!this.client || !this.client.connected) {
      return this.once('connected', () => this[subscribe].bind(this)(subObjects))
    }

    if (!Array.isArray(subObjects)) {
      subObjects = [subObjects]
    }
    const subs = {}

    subObjects.forEach(subObj => {
      const {name, type, live} = subObj
      let topic

      if (type === 'event') {
        topic = util.toTopicName(this.auth.appId, subObj.userId, name, type)
      } else {
        topic = util.toTopicName(this.auth.appId, this.userId, name, type)
      }

      subs[topic] = live ? 0 : 1
    })

    return this.client.subscribe(subs, this.errorHandler.bind(this))
  }

  [unSubscribe](unSubObjects) {
    if (!this.client || !this.client.connected) {
      return this.once('connected', () => this[unSubscribe].bind(this)(unSubObjects))
    }

    if (!Array.isArray(unSubObjects)) {
      unSubObjects = [unSubObjects]
    }
    const unSubs = []

    unSubObjects.forEach(subObj => {
      const {name, type} = subObj
      let topic

      if (type === 'event') {
        topic = util.toTopicName(this.auth.appId, subObj.userId, name, type)
      } else {
        topic = util.toTopicName(this.auth.appId, this.userId, name, type)
      }

      unSubs.push(topic)
    })

    return this.client.unsubscribe(unSubs, this.errorHandler.bind(this))
  }

  [tagsApi](api, ...tags) {
    const isValid = tags.every(tag => typeof tag === 'string')

    if (!isValid) {
      const err = new Error('Chabok error: invalid tag value')

      err.code = 108
      return Promise.reject(err)
    }

    const userId = this.getUserId()

    if (!userId) {
      const err = new Error('Chabok error: user is not registered')

      err.code = 105
      return Promise.reject(err)
    }

    const request = {
      userId,
      tags
    }

    return this.webClient.post('installations/' + api, request)
      .then(results => {
        if (results && results.data) {
          return {
            count: results.data.count || 0
          }
        }
        return {
          count: 0
        }
      })
  }
}

exports.Chabok = Chabok



